<template>
  <div class="home_right_box">
    <!-- <quill-editor
      ref="myQuillEditor"
      v-model="details"
      :options="editorOption"
      @blur="onEditorBlur($event)"
      @focus="onEditorFocus($event)"
      @ready="onEditorReady($event)"
    />-->
    <!-- <quill-editor :content="details" :options="editorOption" @change="onEditorChange($event)" /> -->
    <!--推荐模板标签-->
    <li class="reveal-bottom">
      <div
        class="details"
        v-html="details"
      ></div>
    </li>
  </div>
</template>

<script>
import scrollReveal from "scrollreveal";
// import Quill from "quill";
// import someModule from "../yourModulePath/someQuillModule.js";
// Quill.register("modules/someModule", someModule);
export default {
  data() {
    return {
      scrollReveal: scrollReveal(),
      otherArticleId: this.$route.params.articleId, //详情页的文章id
      articleId: this.$store.getters.getArticleId,
      details: "",
      editorOption: {
        // Some Quill options...
      },
      routeName: this.$route.name
    };
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  watch: {
    "$store.getters.getArticleId": {
      handler: function(newName, oldName) {
        this.articleId = newName;
        this.watchId();
      },
      deep: true,
      immediate: true
    }
  },
  mounted() {
    this.baseData.articleData.forEach(element => {
      if (this.otherArticleId == element.articleId) {
        this.details = element.details;
      }
    });

    this.scrollReveal.reveal(".reveal-bottom", {
      // 动画的时长
      duration: 1000,
      // 延迟时间
      delay: 500,
      // 动画开始的位置，'bottom', 'left', 'top', 'right'
      origin: "bottom",
      // 回滚的时候是否再次触发动画
      reset: false,
      // 在移动端是否使用动画
      mobile: false,
      // 滚动的距离，单位可以用%，rem等
      distance: "20px",
      // 其他可用的动画效果
      opacity: 0.001,
      easing: "linear",
      scale: 0.9
    });
  },
  methods: {
    watchId() {
      this.baseData.articleData.forEach(element => {
        if (this.articleId == element.articleId) {
          this.details = element.details;
        }
      });
    },
    onEditorBlur(quill) {
      // console.log("editor blur!", quill);
    },
    onEditorFocus(quill) {
      // console.log("editor focus!", quill);
    },
    onEditorReady(quill) {
      // console.log("editor ready!", quill);
    },
    onEditorChange({ quill, html, text }) {
      // console.log("editor change!", quill, html, text);
      this.details = html;
      this.baseData.articleData.forEach(element => {
        if (this.articleId == element.articleId) {
          element.details = this.details;
        }
      });
    }
  }
};
</script>
<style lang="scss"  scoped>
.details {
  min-height: calc(100vh + 156px);
}
</style>



